<template>
  <div class="container col-12 offset-0 col-md-10 offset-md-1 col-lg-8 offset-lg-2">
    <!-- ÊêúÁ¥¢Ê°Ü -->
    <PageListSearch
        :valueChange="valueChange"
        :list="list"
    />
    <!-- ‰ø°ÊÅØÂàóË°® -->
    <PageListCards
        :list="list"
        :searchValue="searchValue"
        :addClick="addClick"
        :del-one="delOne"
    />
    <!-- Ê∑ªÂä†ËèúÂçï -->
    <PageListAddForm
        v-show="addOn"
        :allTags="allTags"
        :addOne="addOne"
    />
  </div>
</template>

<script>
import {mapState} from "vuex";
import PageListAddForm from "@/components/page/list/PageListAddForm.vue";
import PageListCards from "@/components/page/list/PageListCards.vue";
import PageListSearch from "@/components/page/list/PageListSearch.vue";
export default {
  name: 'PageList',
  data(){
    return {
      addOn:false,
      searchValue:"",
      list:[]
    }
  },
  components:{
    PageListSearch,
    PageListAddForm,
    PageListCards
  },
  computed:{
    allTags(){
      const data = []
      for (let i of this.list){
        data.push(i.name)
      }
      return data
    },
    ...mapState(["getValueList","removeListOne","addListOne"])
  },
  methods:{
    addClick(){
      this.addOn = true
    },
    valueChange(val){
      this.searchValue = val
    },
    getList(){
      // Â∞ùËØïËé∑Âèñ list ‰ø°ÊÅØ
      this.getValueList(data=>{
        if (data){
          this.list = data
        }else alert("list ‰ø°ÊÅØËØ∑Ê±ÇÂ§±Ë¥•")
      })
    },
    delOne(i){
      if (confirm(`Á°ÆÂÆöË¶ÅÂà†Èô§Á¥¢Âºï ${i.name} ÂêóÔºü`)) {
        // Áî®Êà∑ÁÇπÂáª‰∫Ü‚ÄúÁ°ÆÂÆö‚ÄùÊåâÈíÆÔºåÁªßÁª≠ÊâßË°åÂêéÈù¢ÁöÑ‰ª£Á†Å
        this.removeListOne(i.id,data=>{
          if (!data){
            alert("Â§±Ë¥•")
          }else {
            this.getList()
          }
        })
      }
    },
    addOne(tag,url){
      this.addListOne(tag,url,data=>{
        if(data){
          this.getList()
        }else {
          alert("Â§±Ë¥•")
        }
      })
    }
  },
  mounted(){
    const userVal = JSON.parse(localStorage.getItem("user"))
    if (userVal){
      this.getList()
    }else {
      this.$router.push("/login")
    }
  },
  beforeRouteEnter(to,from,next){
    try {
      const nav = document.getElementById('navbarSupportedContent')
      nav.classList.toggle('show');
    }catch (e) {
      console.log(e)
    }
    next()
  }
}
</script>

