import Vue from "vue";
import Vuex from "vuex";
import axios from "axios";

Vue.use(Vuex)

const actions = {
    // ÂÜôÂÖ•Áî®Êà∑ÂêçÂíåÂØÜÁ†ÅÔºåÂêåÊó∂Âú® localStorage ‰πüÂÜôÂÖ•
    whiteUser(context,value){
        // localStorageÂÜôÂÖ•
        localStorage.setItem("user", JSON.stringify(value));
        context.commit("WHITEUSER",value)
    }
}
const mutations = {
    // ÂÜôÂÖ•Áî®Êà∑ÂêçÂíåÂØÜÁ†Å
    WHITEUSER(state,value){
        state.user.name = value.name
        state.user.password = value.password
    },

}
// ÂèØ‰ª•ÁêÜËß£ÊàêÊâÄÊúâÁªÑ‰ª∂ÈÉΩËÉΩËÆøÈóÆÂà∞ÁöÑdata
const state = {
    user:{
        name:'',
        password:""
    },
    // ÂèëÈÄÅ post ÂëΩ‰ª§
    postTo(path,data,callback){
        // ‰∏∫‰∫ÜÁõ¥Êé•ËÆøÈóÆÊüê‰∏™ÈìæÊé•ÁöÑÂÖÑÂºü‰ª¨ÂÜôÁöÑ
        if (!(data.name || data.password)){
            // Ê≤°ÊúâÊï∞ÊçÆÁöÑËØùÂ∞±ÂÖàÂ∞ùËØïÂéªËØªÂèñÊï∞ÊçÆ
            const userVal = JSON.parse(localStorage.getItem("user"))
            // Â¶ÇÊûúÊúâÊï∞ÊçÆ
            if (userVal) {
                this.user = userVal
                data = userVal
            }
        }
        axios.post(path,data).then(
            res => callback(res.data),
            err => alert("ÁΩëÁªúËøûÊé•Â§±Ë¥•"+err)
        )
    },
    // ÁôªÂΩï‰ΩøÁî®ÁöÑÂáΩÊï∞Ôºå‰ºöËøîÂõûÊàêÂäüÂíåÂ§±Ë¥•
    tryLogIn(name,password,callback){
        this.$store.state.postTo("/systemServer/login",{
            name,password
        },callback)
    },
    // Ëé∑Âèñ list Êï∞ÊçÆ
    getValueList(callback){
        this.$store.state.postTo("/systemServer/getValList",{
            name:this.$store.state.user.name,
            password:this.$store.state.user.password
        },callback)
    },
    // Ëé∑Âèñ log Êï∞ÊçÆ
    getValueLog(callback){
        this.$store.state.postTo("/systemServer/getValLog",{
            name:this.$store.state.user.name,
            password:this.$store.state.user.password
        },callback)
    },
    // Âêë list ‰∏≠Ê∑ªÂä†‰∏Ä‰∏™
    addListOne(tag,url,callback){
        this.$store.state.postTo("/systemServer/addOne",{
            name:this.$store.state.user.name,
            password:this.$store.state.user.password,
            tag,url
        },callback)
    },
    // ‰ªé list ‰∏≠Âà†Èô§‰∏Ä‰∏™
    removeListOne(tagId,callback){
        this.$store.state.postTo("/systemServer/removeOne",{
            name:this.$store.state.user.name,
            password:this.$store.state.user.password,
            tagId
        },callback)
    }

}
// ÂèØ‰ª•ÁêÜËß£ÊàêÊâÄÊúâÁªÑ‰ª∂ÈÉΩËÉΩËÆøÈóÆÂà∞ÁöÑËÆ°ÁÆóÂ±ûÊÄß
const getters = {

}
export default new Vuex.Store({
    actions,
    mutations,
    state,
    getters
})